import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Button} from 'react-bootstrap';
import Banner from './MemberBanner';

const TweenMax = window.TweenMax;
const TimelineMax = window.TimelineMax;

class MembershipPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0);

    // Animate banner
    var banner = document.getElementById("sticky-banner"),
      news = document.getElementsByClassName("news"),
      tl2 = new TimelineMax({repeat: -1});

    tl2
      .to(banner, 0.01, {autoAlpha: 1})
      .staggerFromTo(news, 1,
        {autoAlpha: 0, y: -5},
        {autoAlpha: 1, y: 0},
        5) // News appears and stays for 5s
      .to(news, 0.5, {autoAlpha: 0}, "+=5") // Set delay to make last news stay for 5s
      .to(banner, 0.01, {autoAlpha: 0.5}
    )
  }

  render() {
    return (
      <div className="membership-page margin-bottom">

      <Banner/>

      <Grid className="membership-header space-bottom" fluid={true}>
        <Row>
          <Col md={8} mdOffset={2}>
            <h2 className="uppercase title-underline">Chương trình hội viên</h2>
             <p>
            Đây là sân chơi bổ ích, lành mạnh, đa dạng và thú vị dành cho các hội viên. Các hội viên được tham dự các hoạt động phát triển tài năng và sở trường như tranh biện, hùng biện, nghiên cứu khoa học, kể chuyện, diễn kịch, nhảy múa và các hoạt động nghệ thuật, các hoạt động trải nghiệm như tham quan du lịch hoặc hướng nghiệp, cắm trại; các hoạt động cộng đồng như từ thiện, công tác xã hội, hoạt động bảo vệ môi trường...
            Đây là một kênh cầu nối giữa các hội viên – bố mẹ và hội viên – con, giúp bố mẹ và con cái cùng nhau trải nghiệm, chia sẻ và thấu hiểu, đồng thời giúp các hội viên – bố mẹ chia sẻ những kiến thức, kinh nghiệm nuôi dạy con cái và cập nhật, học hỏi những phương pháp nuôi dạy con tiên tiến và hiệu quả.
            </p>
          </Col>
        </Row>
      </Grid>
      <Grid>
        <Row className="margin-top">
          <Col md={6} mdOffset={1}>
            <h3>Đối tượng tham gia</h3>
            <ul>
              <li>Hội viên: là các em học sinh trong độ tuổi Tiểu học và THCS đã đăng ký tham gia Chương trình Hội viên PDI.</li>
              <li>Phụ huynh hội viên: là các bố các mẹ của các hội viên PDI</li>
            </ul>

            <h3>Mục tiêu</h3>
            Mục tiêu Chương trình Hội viên PDI là đồng hành giúp các hội viên trở thành những cá nhân:
            <ul>
              <li>Biết tự chăm sóc và bảo vệ bản thân</li>
              <li>Yêu và tự hào về bản thân, khám phá và làm chủ bản thân</li>
              <li>Gần gũi với thiên nhiên, khám phá và bảo vệ môi trường tự nhiên</li>
              <li>Sống chia sẻ, có trách nhiệm với bản thân và cộng đồng,</li>
              <li>Nhận biết và phát huy các giá trị sống</li>
            </ul>
            <p>Đồng thời, Chương trình cũng đồng hành cùng với các phụ huynh hội viên trong quá trình nuôi dạy con cái, góp phần thúc đẩy và tôn vinh nền tảng giáo dục gia đình là giá trị cốt lõi.</p>

            <h3>Hình thức hoạt động</h3>
            <ul>
              <li>Chương trình hội viên PDI được tổ chức dành cho các hội viên và phụ huynh hội viên thông qua các hoạt động online và offline</li>
              <li>Các hội viên sẽ được sinh hoạt offline 2 buổi/1 tháng</li>
              <li>Tổ chức hoạt động trải nghiệm, kết nối với nhóm/ tổ chức/ CLB khác tại các địa bàn khác giúp trẻ biết đồng cảm và chia sẻ.</li>
              <li>Hàng quý, PDI sẽ tổ chức coaching 1-1 cho từng hội viên. PDI sẽ cùng hội viên xây dựng chương trình phát triển cá nhân của từng hội viên, lên kế hoạch thực hiện cho hội viên, đánh giá lại và điều chỉnh sau từng quý.</li>
              <li>Tùy thuộc vào đánh giá Hội viên từng quý, nhóm tư vấn PDI sẽ cần sự đồng hành và phối hợp của cha mẹ.</li>
            </ul>

            <h3>Quyền lợi của hội viên</h3>
            <ul>
              <li><span className="highlight-text bold">Miễn phí</span> cho hội viên và/hoặc phụ huynh hội viên khi tham dự các buổi sinh hoạt của câu lạc bộ, dự kiến tổ chức 2 lần/ 1 tháng</li>
              <li><span className="highlight-text bold">Coaching cho từng hội viên</span> 1 lần/quý.</li>
              <li><span className="highlight-text bold">Giảm 50%</span> cho hội viên và phụ huynh hội viên khi tham dự các hội thảo do PDI tổ chức có mời diễn giả nổi tiếng</li>
              <li><span className="highlight-text bold">Giảm 30%</span> cho hội viên và phụ huynh hội viên khi tham gia tham quan dã ngoại</li>
              <li>Được tham gia diễn đàn của câu lạc bộ trên website và fanpage</li>
            </ul>

            <h3>Chứng nhận Thành tích</h3>
            <p>Thông qua các khóa học và chương trình câu lạc bộ, Hội viên sẽ hình thành các kỹ năng sống, tích lũy để dành được những huy hiệu về các kỹ năng mà hội viên đã thuần thục.</p>
          </Col>

          <Col md={4}>
            <div className="highlight-panel">
              <h3>10 giá trị đạo đức Chương trình Hội viên PDI</h3>
              <dl className="space-top">
                <dt>TRUSTWORTHY - Đáng tin cậy</dt>
                <dd>Là một người mà gia đình, bạn bè và xã hội có thể tin tưởng, trông cậy. Luôn hành động và thực hiện những điều mình cam kết trước đó.</dd>
                <dt>HONESTY - Trung thực</dt>
                <dd> Có thái độ ngay thẳng, thật thà khi giao tiếp với người thân trong gia đình, bạn bè và những người xung quanh.</dd>
                <dt>HELPFULNESS - Hữu ích</dt>
                <dd> Sẵn sàng giúp đỡ người thân trong gia đình, bạn bè hoặc người xung quanh trong phạm vi khả năng cho phép.</dd>
                <dt>FRIENDLY - Thân thiện</dt>
                <dd> Có hành vi cư xử lịch thiệp và thân thiện với tất cả mọi người, không phân biệt màu da, giới tính, tuổi tác,…</dd>
                <dt>KINDNESS - Lòng tốt</dt>
                <dd> Tỏ ra hào phóng, ân cần và thân thiện với tất cả mọi người. Nghiêm cấm những hành vi xâm hại, làm tổn thương người hoặc động vật xung quanh.</dd>
                <dt>COMPLIANCE - Tính tuân thủ kỷ luật</dt>
                <dd> Tuân thủ và chấp hành những quy định được đề ra trong gia đình, trường học hoặc trong các đoàn/đội/tập thể.</dd>
                <dt>OPTIMISTIC - Tinh thần lạc quan</dt>
                <dd> Nhìn nhận các mặt tích cực của vấn đề/sự việc. Có thái độ cởi mở, vui vẻ trong các hoạt động hàng ngày và cố gắng đem lại niềm vui cho mọi người.</dd>
                <dt>THOUGHTFULNESS - Sự chín chắn:</dt>
                <dd> Luôn thận trọng, suy nghĩ thấu đáo khi đối diện với một vấn đề/ sự vật/ sự việc.</dd>
                <dt>COURAGE - Can đảm</dt>
                <dd> Sẵn lòng đối mặt với những thách thức trong cuộc sống. Biết đấu tranh cho những hành vi, ứng xử hay lối sống, cách nghĩ đúng đắn.</dd>
                <dt>HEALTHY - Lành mạnh</dt>
                <dd> Bảo vệ sức khỏe của bản thân và gia đình. Giữ gìn nhà cửa và môi trường cộng đồng sạch đẹp.</dd>
              </dl>
            </div>
          </Col>
          <Col md={12} className="clearfix">
            <div className="reg-container margin-top text-center">
              <Button href="#/contact" bsClass="reg-button text-large">Đăng kí</Button>
            </div>
          </Col>
        </Row>
      </Grid>
    </div>
    );
  }
}

MembershipPage.route = <Route exact path="/membership" component={MembershipPage}/>;

export default MembershipPage;



