import React from 'react';
import {Link} from 'react-router-dom';
import {Grid, Row, Col, Thumbnail, Button} from 'react-bootstrap';

import courses from './courses.json';


const CourseTeaser = ({course}) => (
  <Col sm={4} md={4}>
    <Thumbnail alt="Course" src={`./img/courses/course${course.id}.jpg`} className="course" >
      <h3>{course.name}</h3>
      <div className="button-holder">
        <Button href={`/#/courses/${course.id}`} bsClass="more-button">Xem thêm</Button>
      </div>
    </Thumbnail>
  </Col>
);

const CourseTeasers = ({titled, limit}) => {
  return (
    <div className="courses">
    <Grid className="space-bottom">
    <Row className="clearfix">
      {titled && <h2 className="highlight-text text-center text-capitalize">Trải nghiệm hè 2017 cùng PDI</h2>}
    </Row>
    <Row className="clearfix space-top">
      {courses.slice(0, 3).map(c => <CourseTeaser key={c.id} course={c} />)}
    </Row>

    <Row className="clearfix">
      {courses.slice(3, 5).map(c => <CourseTeaser key={c.id} course={c} />)}
    </Row>
    <Row className="clearfix">
      {limit > 0 && limit < courses.length &&
        <p className="uppercase more"><Link to="/courses">Xem thêm...</Link></p>
      }
    </Row>
    </Grid>
    </div>
  );
};

export default CourseTeasers;



