import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Glyphicon, Thumbnail} from 'react-bootstrap';

import coaches from './coaches.json';

class CoachPage extends Component {
  constructor(props) {
    super(props);

    const coachId = props.match.params.id;
    const coach = coaches.find(c => c.id === coachId);

    if (coach === undefined) {
      window.location = '/';
    }

    this.state = coach;

    fetch(`/coaches/${coachId}.html`)
      .then(response => response.text())
      .then(intro => this.setState({intro}));
  }

  componentDidMount () {
    window.scrollTo(0, 0)
  }

  render() {
    return (
      <Grid>
        <Row>
          <h2 className="title-underline uppercase">Giới thiệu về giảng viên</h2>
        </Row>
        <Row>
          <Col md={4} mdOffset={2}>
            <Thumbnail className="coach-teaser" alt="coach" src={`./img/coaches/coach${this.state.id}.jpg`}>
              <h3>{this.state.name}</h3>
              <blockquote>
                <p>{this.state.teaser}</p>
                <footer className="author">{this.state.author}</footer>
              </blockquote>
            </Thumbnail>
            </Col>
            <Col md={4}>
              <div dangerouslySetInnerHTML={{__html: this.state.intro}} />
            </Col>
        </Row>
        <Row>
          <Col md={8} mdOffset={2}>
            <p className="pull-right">
              <a href={`/#/coaches`} className="uppercase link-back"><Glyphicon glyph="arrow-left" />Đội ngũ giảng viên</a>
            </p>
          </Col>
        </Row>
      </Grid>
    );
  }
}

CoachPage.route = <Route exact path="/coaches/:id" component={CoachPage}/>;

export default CoachPage;



