import React from 'react';
import {Link} from 'react-router-dom';
import {Grid, Row, Navbar, Nav, NavItem, NavDropdown, MenuItem} from 'react-bootstrap';

import Logo from './img/logo.svg';

import {withoutAnchor} from './anchor';

const navItem = (text, href) => (
  <NavItem
    href={href}
    active={withoutAnchor(window.location.href).endsWith(href)}
    onClick={() => window.location = href}
  >
    {text}
  </NavItem>
);

const menuItem = (text, href) => (
  <MenuItem
    href={href}
    active={withoutAnchor(window.location.href).endsWith(href)}
    onClick={() => window.location = href}
  >
    {text}
  </MenuItem>
);

const Header = () => {
  return (
    <Grid fluid={true}>
      <Row>
        <div id="header">
          <Navbar fluid={true} fixedTop={true} collapseOnSelect>
            <Navbar.Header>
              <Link to="/" className="navbar-left"><img id="logo" alt="PDI Logo" width="60" height="60" src={Logo} /></Link>
              <Navbar.Text id="tagline" className="navbar-left">Đồng hành trên mỗi bước trưởng thành</Navbar.Text>
              <Navbar.Toggle />
            </Navbar.Header>

            <Navbar.Collapse>
              <Nav pullRight>
                {navItem('Trang chủ', '/#/')}

                <NavDropdown title="Giới thiệu" id="about-dropdown" href="/#/about">
                  {menuItem('Giới thiệu chung', '/#/about')}
                  {menuItem('Chiến lược kinh doanh', '/#/about@about-vision')}
                  {menuItem('Triết lý kinh doanh', '/#/about@about-philosophy')}
                  {menuItem('Đội ngũ giáo viên', '/#/about@about-team')}
                  {menuItem('Giới thiệu về logo', '/#/about@about-logo')}
                </NavDropdown>

                <NavDropdown title="Sản phẩm" id="courses-dropdown" href="/#/courses">
                  {menuItem('Nền tảng phương pháp giáo dục', '/#/courses')}
                  {menuItem('Giới thiệu chung', '/#/courses@about-courses')}
                  {/* {menuItem('Chương trình hội viên', '/#/courses@membership')} */}
                  {menuItem('Dịch vụ tham vấn tâm lý', '/#/courses@consulting')}
                  {menuItem('Các khoá học', '/#/courses@courses-list')}
                </NavDropdown>

                {navItem('Hội viên', '/#/membership')}
                {navItem('Sự kiện', '/#/events')}
                {navItem('Chia sẻ', '/#/blog')}
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </div>
      </Row>
    </Grid>
  );
};

export default Header;



