import React from 'react';
import {HashRouter as Router} from 'react-router-dom';

//import {Grid} from 'react-bootstrap';

import TopPage from './TopPage';
import AboutPage from './AboutPage';
import CoursesPage from './CoursesPage';
import CoachesPage from './CoachesPage';
import CoachPage from './CoachPage';
import CoursePage from './CoursePage';
import ContactPage from './ContactPage';
import EventsPage from './EventsPage';
import EventPage from './EventPage';
import MembershipPage from './MembershipPage';

import Header from './Header';
import Footer from './Footer';

const App = () => {
  return (
    <Router>
      <div>
        <Header />

        {TopPage.route}

        {AboutPage.route}
        {AboutPage.routeAnchorAboutTeam}
        {AboutPage.routeAnchorAboutVision}
        {AboutPage.routeAnchorAboutPhilosophy}
        {AboutPage.routeAnchorAboutLogo}

        {CoursesPage.route}
        {CoursesPage.routeAnchorCoursesList}
        {CoursesPage.routeAnchorAboutCourses}
        {CoursesPage.routeAnchorMembership}
        {CoursesPage.routeAnchorConsulting}

        {CoachesPage.route}

        {CoachPage.route}

        {CoursePage.route}

        {ContactPage.route}

        {EventsPage.route}
        {EventPage.route}

        {MembershipPage.route}

        <Footer />
      </div>
    </Router>
  );
};

export default App;



