import React from 'react';
import {Row, Col, Grid, Button} from 'react-bootstrap';

const ProductIntro = () => {
  return (
    <div className="product-intro">
      <Grid>
        <Row>
          <Col md={10} mdOffset={1}>
          <h2 className="uppercase title-underline">Giới thiệu chung</h2>
          <p>Học viên Phát triển Cá nhân PDI được thành lập với mong muốn đồng hành với các học viên trong độ tuổi tiểu học và trung học cơ sở, trang bị cho các con những kỹ năng sống cần thiết để có thể sinh tồn trong thế giới tự nhiên và tự tin hòa nhập với môi trường xã hội. PDI sẽ giúp tạo dựng môi trường cho các học viên được rèn luyện và trau dồi các kỹ năng sống đã được học; giao lưu, kết bạn, chia sẻ những niềm đam mê sở thích.</p>
          <p>PDI mong muốn được là cầu nối giúp các cha mẹ có thể thấu hiểu để chủ động đồng hành cùng các con.</p>
          <p>PDI sẽ tạo lập một cộng đồng giúp các học viên có thể trau dồi, rèn luyện và chia sẻ kiến thức, kỹ năng, và những giá trị tốt đẹp; đồng thời kết nối các cha mẹ có cùng mong muốn được đồng hành cùng các con, chia sẻ những kiến thức, kinh nghiệm, phương pháp nuôi dạy con.</p>
          </Col>
        </Row>

        <Row className="clearfix space-top products text-center">
          <Col md={4} className="product-item">
            <div className="icon"><img alt="first aid" src={"./img/tent-night.svg"} width="180" height="180" /></div>
            <div className="text-panel">
              <h2 className="text-capitalize">Chương trình giảng dạy kỹ năng sống</h2>
              <p>Chương trình giảng dạy kỹ năng sống tại PDI được thiết kế riêng cho trẻ em Việt Nam, phù hợp và bổ trợ cho hệ thống giáo dục cấp cơ sở, dựa trên nền tảng kết hợp chương trình hướng đạo cho thanh thiếu niên sáng lập bởi Bader Powell và học thuyết Trí thông minh đa dạng của Howard Gardner, thông qua phương pháp học tập qua trải nghiệm.</p>
              <p>Chương trình tập trung phát triển các nhóm kỹ năng thiết yếu cho trẻ em ở độ tuổi từ 8 đến 14 tuổi nhằm giúp các em tự tin và tự chủ ứng phó với mọi tình huống gặp phải trong cuộc sống hàng ngày.</p>
            </div>
            <div className="reg-container">
              <Button href="#courses@about-courses" bsClass="reg-button uppercase">Chi tiết</Button>
            </div>
          </Col>

          <Col md={4} className="product-item">
            <div className="icon"><img alt="first aid" src={"./img/chatting.svg"} width="180" height="180" /></div>
            <div className="text-panel">
              <h2 className="text-capitalize">Dịch vụ tham vấn tâm lý</h2>
              <p>PDI tự hào là một trong những tổ chức đầu tiên cung cấp dịch vụ tham vấn tâm lý, với mong muốn mang tới cơ hội cho các phụ huynh và học viên của mình có thể tham vấn và nhận hỗ trợ trực tiếp từ chuyên gia tâm lý hàng đầu. Chỉ cần đặt lịch, các bậc phụ huynh và các học viên sẽ được cùng chuyên gia tâm lý của chúng tôi chuyện trò, chia sẻ, tìm hiểu gốc rễ các câu chuyện, vấn đề, khó khăn, vướng mắc của bản thân và được tư vấn giải pháp xử lý.</p>
              <p>Trong quá trình học tại PDI, các học viên sẽ được nhận xét đánh giá và tư vấn bởi chuyên gia tâm lý của PDI.</p>
            </div>
            <div className="reg-container">
              <Button href="#/contact" bsClass="reg-button uppercase">Đăng kí</Button>
            </div>
          </Col>

          <Col md={4} className="product-item">
            <div className="icon"><img alt="first aid" src={"./img/person.svg"} width="180" height="180" /></div>
            <div className="text-panel">
              <h2 className="text-capitalize">Chương trình hội viên PDI</h2>
              <p>Chương trình hội viên PDI được tổ chức dành cho các học viên và phụ huynh thông qua các hoạt động online và offline nhằm giúp các học viên được trau dồi các kỹ năng sống đã được đào tạo tại PDI, tổ chức các hoạt động chia sẻ trách nhiệm với cộng đồng,...</p>
              <p>Ngoài ra, PDI cũng sẽ thường xuyên chia sẻ các kiến thức về nuôi dạy con cái và thường thức trong gia đình cho các bố mẹ, nhằm cung cấp các kiến thức nền giúp bố mẹ có thể chủ động đồng hành cùng các con.</p>
            </div>
            <div className="reg-container">
              <Button href="#/membership" bsClass="reg-button uppercase">Chi tiết</Button>
            </div>
          </Col>
        </Row>
      </Grid>
    </div>
  );
};

export default ProductIntro;



