import React from 'react';
import {Grid, Row, Col, Image, Glyphicon} from 'react-bootstrap';

import events from './events.json';


const EventTeaser = ({event}) => (
  <Row className="clearfix event-item">
    <Col sm={4} md={4}>
      <a href={`/#/events/${event.id}`}><Image responsive alt="Event" src={`./img/events/event${event.id}.jpg`} className="event" /></a>
    </Col>
    <Col md={6}>
      <h3><a href={`/#/events/${event.id}`}>{event.name}</a></h3>
      <p><Glyphicon glyph="map-marker"/>{event.location}</p>
      <p>{event.teaser}</p>
    </Col>
    <Col md={2}>
      <h4 className="date"><Glyphicon glyph="calendar"/>{event.date}</h4>
      <p>{event.start}-{event.end}</p>
    </Col>
  </Row>
);

const EventTeasers = ({titled, limit}) => {
  return (
    <div className="events">
      <Grid className="space-bottom">
        {events.reverse().map(c => <EventTeaser key={c.id} event={c} />)}
      </Grid>
    </div>
  );
};

export default EventTeasers;



