import React from 'react';
import {Grid, Row, Carousel} from 'react-bootstrap';

import SlideImage1 from './img/slider/website-01.jpg';
import SlideImage2 from './img/slider/website-02.jpg';
import SlideImage3 from './img/slider/website-03.jpg';
import SlideImage4 from './img/slider/website-04.jpg';
import SlideImage5 from './img/slider/website-05.jpg';

const IntroSlider = () => {
  return (
    <div id="slide-container">
      <Grid fluid={true}>
        <Row id="intro-slider">
          <Carousel interval={5000}>
            <Carousel.Item>
              <img alt="" width={1170} src={SlideImage1} />
            </Carousel.Item>
            <Carousel.Item>
              <img alt="" width={1170} src={SlideImage2} />
            </Carousel.Item>
            <Carousel.Item>
              <img alt="" width={1170} src={SlideImage3} />
            </Carousel.Item>
            <Carousel.Item>
              <img alt="" width={1170} src={SlideImage4} />
            </Carousel.Item>
            <Carousel.Item>
              <img alt="" width={1170} src={SlideImage5} />
            </Carousel.Item>
          </Carousel>
        </Row>
      </Grid>
    </div>
  );
};

export default IntroSlider;



