import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Glyphicon, Carousel, Button} from 'react-bootstrap';

import courses from './courses.json';

const carouselItems = (course) => {
  const range = [...Array(course.slides).keys()];
  return range.map((picIdx) => (
    <Carousel.Item key={picIdx}>
      <img alt="" width={600} height={400} src={`/img/courses/${course.id}/${course.id}_${picIdx + 1}.jpg`} />
    </Carousel.Item>
  ))
};

class CoursePage extends Component {
  constructor(props) {
    super(props);

    const courseId = props.match.params.id;
    const course = courses.find(c => c.id === courseId);

    if (course === undefined) {
      window.location = '/';
    }

    this.state = course;

    fetch(`/courses/${courseId}.html`)
      .then(response => response.text())
      .then(intro => this.setState({intro}));
  }

  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <Grid>
        <Row>
          <h2 className="title-underline uppercase">Các khoá học hè 2017</h2>
        </Row>
        <Row>
          <Col md={12} className="text-center">
            <h2 className="highlight-text">{this.state.name}</h2>
            <h3 className="uppercase">{this.state.slogan}</h3>
          </Col>
         </Row>
        {this.state.slides > 0 &&
          <Row className="space-top">
            <Col md={6} mdOffset={3}>
              <Carousel interval={3500}>
                {carouselItems(this.state)}
              </Carousel>
            </Col>
          </Row>
        }
        <Row>
          <Col md={10} mdOffset={1} className="no-top-margin">
            <div dangerouslySetInnerHTML={{__html: this.state.intro}} />
          </Col>
        </Row>
        <Row className="space-top">
          <Col md={10} mdOffset={1} className="text-center">
          <div className="reg-container">
              <Button href="#/contact" bsClass="reg-button text-large">Đăng kí để biết thêm chi tiết</Button>
            </div>
            <p className="pull-right">
              <a href={`/#/courses@courses-list`} className="uppercase link-back"><Glyphicon glyph="arrow-left" />Các khoá học</a>
            </p>
          </Col>
        </Row>
      </Grid>
    );
  }
}

CoursePage.route = <Route exact path="/courses/:id" component={CoursePage}/>;

export default CoursePage;



