import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import EventTeasers from './EventTeasers';

class EventsPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      lightboxOpen: false,
      currentImage: 0
    };
  }

  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div id="events-list">
        <h2 className="title-underline uppercase">Các sự kiện gần đây</h2>
        <EventTeasers limit={-1} />
      </div>
    );
  }
}

EventsPage.route = <Route exact path="/events" component={EventsPage}/>;

export default EventsPage;



