import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Button} from 'react-bootstrap';

import CourseTeasers from './CourseTeasers';
import anchor from './anchor';

class CoursesPage extends Component {
  componentDidMount () {
    anchor('courses-list', 'about-courses', 'membership', 'consulting');
  }

  render() {
    return (
      <div className="courses-page">
        <div className="parallax first">
          <div className="title-box">
                <span className="white-text"><h2 className="uppercase">Nền tảng phương pháp giáo dục</h2></span>
          </div>
        </div>

        <div className="intro text-center">
          <Grid>
            <Row>
              <Col md={10} mdOffset={1}>
                <h2>Chương trình Hướng đạo - Scouting Movement</h2>
                <p className="text-large">Bắt đầu từ ý tưởng ấp ủ của một vị tướng người Anh – Baden Powell – chương trình này đã và đang thu hút hàng triệu thanh thiếu nhi trên thế giới. Chương trình Hướng Đạo được xây dựng nhằm khơi nguồn và phát huy mọi khả năng của các em mà không phải dồn ép, đè nén đồng thời hướng tới mục tiêu giúp các em giỏi trên nhiều lĩnh vực khác nhau mà không chỉ là xuất sắc trên một phương diện.</p>
                <p className="text-large">Thông qua các hoạt động mới mẻ, các bài giảng độc đáo, chương trình này đã góp phần vào việc giáo dục thanh thiếu niên bằng cách giúp họ phát huy toàn vẹn các kỹ năng về thể chất, xã hội và tinh thần. Qua đó dẫn dắt các em trở thành “những công dân khỏe mạnh, vui vẻ và giúp ích” cho cộng đồng địa phương, cho quốc gia và quốc tế.</p>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1}>
                <h2>Học thuyết Trí thông minh đa dạng - Multiple Intelligences</h2>
                <p className="text-large">Các nghiên cứu của Giáo sư Howard Gardner, một nhà tâm lí học nổi tiếng của Đại học Harvard đã có những ảnh hưởng sâu sắc đến những hoạt động giáo dục trên thế giới. Ông cho rằng có đến 9 loại hình thông minh: Trí thông minh về toán học/logic, trí thông minh về ngôn ngữ/ lời nói, trí thông minh về thị giác/ không gian, trí thông minh về vận động/ thể chất, trí thông minh về âm nhạc/ giai điệu… Quan điểm của ông là chúng ta phải phân tích những hiểu biết về chính bản thân mình để từ đó hiểu chúng ta là ai và chúng ta có thể làm được gì.</p>
              </Col>
            </Row>
            <Row>
                <Col md={10} mdOffset={1}>
                  <h2>Phương pháp học tập qua trải nghiệm - Experiential Learning</h2>
                  <p className="text-large">Học tập qua trải nghiệm giúp học sinh tự tìm tòi, khám phá tri thức cũng như kĩ năng thông qua các hoạt động trải nghiệm thực tế. Người thầy sẽ chỉ đóng vai trò chuyên gia cố vấn, hướng dẫn, hỗ trợ, thiết lập và đánh giá tiêu chuẩn cho việc học và vai trò huấn luyện “coaching”. Sự khác biệt của phương pháp này so với các phương pháp giáo dục mô phạm chính là các bước đúc kết sau quá trình trải nghiệm. Mỗi bước bao gồm các câu hỏi mở được đưa ra để học sinh trả lời, khiến học sinh phải thực sự động não, từ đó tự rút ra bài học cho bản thân. Đây cũng là lúc để đánh giá lại quá trình trải nghiệm của người học. Các câu hỏi rất đa dạng tùy theo từng hoạt động cụ thể. Phương pháp và các bước có thể áp dụng với tất cả các chủ đề, lĩnh vực, tùy theo định hướng của người thiết kế.</p>
                </Col>
              </Row>
          </Grid>
        </div>

        <div className="parallax pic3 less-height">
        </div>

        <div className="parallax pic3 first" id="about-courses">
          <div className="title-box">
                <span className="black-text"><h2 className="uppercase">GIỚI THIỆU CHUNG VỀ CÁC KHÓA HỌC CỦA PDI</h2></span>
          </div>
        </div>

        <div className="intro">
          <Grid>
            <Row>
              <Col className="text-center">
                <h2>NHÓM KỸ NĂNG THỰC HÀNH XÃ HỘI - “BUILD INNER POWER”</h2>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1} className="text-center">
                <div className="icon"><img alt="first aid" src={`./img/people.svg`} width="180" height="180" /></div>
                <p className="text-large">Cuộc sống ngày càng phát triển đòi hỏi mỗi cá nhân phải tự hoàn thiện bản thân để kịp thời thích ứng và đón đầu các xu hướng mà sự phát triển tuyệt vời đó mang lại. Tuy nhiên, sự hoàn thiện đó không thể được hoàn thành nếu các kỹ năng thực hành xã hội không được chú ý và phát triển đúng mức.</p>
                <p className="text-large">Khóa học Kỹ năng Thực hành Xã hội của PDI được xây dựng để bổ sung, hỗ trợ và góp phần phát triển các kỹ năng thiết yếu đó cho các học viên của chúng tôi, bù đắp cho sự thiếu hụt của không nhiều các chương trình đào tạo kỹ năng cho trẻ em hiện có ở Việt Nam.</p>
                <p className="text-large">Tham gia vào khóa học, các em được trang bị đầy đủ các kiến thức cần thiết để hiểu, yêu, và trân trọng bản thân mình; được rèn luyện để phát triển khả năng giao tiếp, suy nghĩ sáng tạo, quản lý thời gian, tài chính, đưa ra quyết định, và lãnh đạo. Với 10 buổi học chú trọng việc để mỗi học sinh tự thể hiện mình và phát triển bản thân là trung tâm, sau khóa học các em chắc chắn sẽ có hiểu biết và phát triển các năng lực nội tại của bản thân, làm chủ bản thân, biết quản lý thời gian, công việc, cảm xúc của mình, cũng như trau dồi các kỹ năng cần thiết nêu trên để luôn tự tin và thành công với các quyết định đúng đắn và các kế hoạch cụ thể đã đề ra.</p>
              </Col>
            </Row>
          </Grid>
        </div>

        <div className="parallax pic3 less-height">
        </div>

        <div className="intro">
          <Grid>
            <Row>
              <Col className="text-center">
                <h2>NHÓM KỸ NĂNG KHÁM PHÁ TỰ NHIÊN - “TOUCH THE NATURE“</h2>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1} className="text-center">
                <div className="icon"><img alt="nature" src={`./img/nature.svg`} width="180" height="180" /></div>
                <p className="text-large">Đây là nhóm khóa học được thiết kế đặc biệt cho các em học sinh, gồm 2 nội dung lớn: Khoa học tự nhiên và Kỹ năng sinh tồn.</p>
                <p className="text-large">Mỗi khóa học sẽ diễn ra trong 10 buổi với các giờ học trên lớp và ngoài trời bổ ích, nhằm trang bị cho các em học sinh các kiến thức và kỹ năng cần thiết để các em hiểu biết về các hiện tượng, sự vật trong thiên nhiên, đam mê tìm tòi, khám phá tự nhiên, có kỹ năng xây dựng kế hoạch và thực hiện thông qua các chuyến dã ngoại do chính các em lên kế hoạch, và quan trọng hơn cả là các em có khả năng sinh tồn trong tự nhiên.</p>
                <p className="text-large">Tiên tiến hơn so với mô hình học tập truyền thống, nhóm khóa học KỸ NĂNG KHÁM PHÁ TỰ NHIÊN chú trọng các hoạt động tạo cơ hội cho các em học sinh tự trải nghiệm để ứng dụng ngay các kiến thức học được vào khám phá và vui chơi cùng tự nhiên kỳ thú. Thông qua hình thức học tập trải nghiệm này, các em sẽ có các kỹ năng sử dụng sách hướng dẫn, có thể nhận biết và phân biệt những nhóm sinh vật có lợi/ có hại, và cả những địa điểm tiềm ẩn các mối nguy hiểm ngoài tự nhiên. Các em cũng được phát triển để có thể tự mình lên kế hoạch, chuẩn bị tốt cho các buổi đi chơi cùng gia đình/ nhóm/ hội bên ngoài, cũng như có các kỹ năng thiết yếu để có thể sinh tồn trong lòng tự nhiên (bơi lội, đi rừng, cắm trại, …). Bên cạnh đó, khóa học cũng khơi nguồn và giúp các em nuôi dưỡng tình yêu với thiên nhiên, kích thích mong muốn tim tòi và khám phá tự nhiên, nâng cao khả năng thích ứng, và có những ứng xử phù hợp với tự nhiên (đề phòng, cảnh giác cao với các mối nguy hiểm, tự tin, bình tĩnh hòa mình với tự nhiên, bảo vệ môi trường sống, ...).</p>
              </Col>
            </Row>
          </Grid>
        </div>

        <div className="parallax pic3 less-height">
        </div>

        <div className="intro">
          <Grid>
            <Row>
              <Col className="text-center">
                <h2>NHÓM KỸ NĂNG AN TOÀN - “STAY SECURED LIVE SMART”</h2>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1} className="text-center">
                <div className="icon"><img alt="first aid" src={`./img/first-aid.svg`} width="180" height="180" /></div>
                <p className="text-large">Đây là nhóm kỹ năng tối quan trọng được PDI nghiên cứu giảng dạy nhằm trang bị cho mỗi học viên các kiến thức và kỹ năng cần thiết để các em có thể tự bảo vệ bản thân cũng như các cá nhân khác trong xã hội.</p>
                <p className="text-large">Sơ Cấp Cứu, gồm hơn 20 chuyên đề, là 1 trong 3 nội dung chính của nhóm kỹ năng này. Sau mỗi buổi học với các kiến thức thực tiễn và các giờ tự thực hành ngay tại lớp do các giảng viên giàu kinh nghiệm của PDI trực tiếp hướng dẫn và giám sát, các em sẽ nắm được và có thể thực hiện chính xác các kỹ thuật sơ cấp cứu được giảng dạy trong buổi học đó.</p>
                <p className="text-large">Nội dung thứ 2 được giảng dạy là Kỹ năng thoát hiểm. Trong khóa học này, các em sẽ được hướng dẫn chi tiết, cụ thể, và thực hành các kỹ năng thiết yếu để có thể tự giúp bản thân mình thoát ra khỏi các trường hợp khẩn cấp và nguy hiểm như ở trong thang máy gặp sự cố, gặp hỏa hoạn trong tòa nhà, bị đe dọa, bắt cóc, hay lạc đường, ...</p>
                <p className="text-large">Kỹ năng An toàn trong xã hội được xây dựng để đáp ứng nhu cầu cấp thiết của các bậc cha mẹ trong xã hội hiện tại. Các nội dung giáo dục giới tính, phòng tránh xâm hai tình dục, giáo dục về chất kích thích/gây nghiện và kỹ năng phòng tránh là các nội dung chủ đạo của khóa học này. Đúng như cái tên của khóa học đã được nhóm chuyên môn của PDI cân nhắc và lựa chọn rất kỹ lưỡng, khóa học sẽ trang bị cho các em các kiến thức, kỹ năng và tạo thành phản xạ cần thiết để có thể AN TOÀN trong chính xã hội của mình. Trong khóa học kỹ năng an toàn xã hội, các em học sinh sẽ được hướng dẫn để có thể nhận diện nhanh nhạy và chính xác các nguy cơ nguy hiểm trong xã hội, biết tự bảo vệ mình, phòng ngừa khỏi các nguy cơ đó. Bên cạnh đó, các em cũng được trang bị kiến thức về cơ thể, giới tính, giai đoạn phát triển bản thân để biết yêu thương chính mình, sống tự tin, làm chủ và học nếp sống lành mạnh, văn minh.</p>
              </Col>
            </Row>
          </Grid>
        </div>

        <div className="parallax pic3 less-height">
        </div>

        <div className="intro white" id="consulting">
          <Grid>
            <Row>
              <Col className="text-center">
                <h2>DỊCH VỤ THAM VẤN TÂM LÝ</h2>
              </Col>
            </Row>
            <Row>
              <Col md={10} mdOffset={1} className="text-center">
                <p className="text-large">DỊCH VỤ THAM VẤN TÂM LÝ là một trong những mảng rất riêng, rất khác biệt, và đáng tự hào của PDI.</p>
                <p className="text-large">Là một trong những tổ chức đầu tiên cung cấp dịch vụ này, PDI mong muốn mang tới cơ hội được tham vấn và hỗ trợ trực tiếp từ chuyên gia tâm lý hàng đầu cho tất cả các bậc phụ huynh và các em học sinh. Chỉ cần đặt lịch trước, các bậc phụ huynh và các em học sinh sẽ được cùng chuyên gia tâm lý của chúng tôi chuyện trò, chia sẻ, tìm hiểu gốc rễ các câu chuyện, vấn đề, khó khăn, vướng mắc của bản thân và được tư vấn giải pháp xử lý.</p>
                <p className="text-large">Dịch vụ này cùng với tâm sức của tất cả những người xây dựng và các chuyên gia hàng đầu chắc chắn sẽ khiến cho tất cả các đối tác có cơ hội trải nghiệm thấy hài lòng.</p>
                <div className="reg-container margin-top text-center">
                  <Button href="#/contact" bsClass="reg-button text-large">Đăng kí</Button>
                </div>
              </Col>
            </Row>
          </Grid>
        </div>

        <div className="parallax pic3 less-height">
        </div>

        <div id="courses-list">
          <h2 className="title-underline uppercase">Giới thiệu các khoá học hè 2017</h2>
          <CourseTeasers limit={-1} />
        </div>
      </div>
    );
  }
}

CoursesPage.route = <Route exact path="/courses" component={CoursesPage}/>;
CoursesPage.routeAnchorCoursesList = <Route exact path="/courses@courses-list" component={CoursesPage}/>;
CoursesPage.routeAnchorAboutCourses = <Route exact path="/courses@about-courses" component={CoursesPage}/>;
CoursesPage.routeAnchorMembership = <Route exact path="/courses@membership" component={CoursesPage}/>;
CoursesPage.routeAnchorConsulting = <Route exact path="/courses@consulting" component={CoursesPage}/>;

export default CoursesPage;



