import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import IntroSlider from './IntroSlider';
import ProductIntro from './ProductIntro';
import CourseTeasers from './CourseTeasers';
import CoachTeasers from './CoachTeasers';
import Banner from './Banner';
import TopBanner from './TopBanner';
import Map from './Map';

const TweenMax = window.TweenMax;
const TimelineMax = window.TimelineMax;
const ScrollMagic = window.ScrollMagic;

class TopPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0);

    // const tl2 = new TimelineMax()
    // const elems = document.getElementsByClassName('banner-content');
    // for (var i = 0; i < elems.length; i++) {
    //   let e = elems[i];

    //   tl2.to(e, 1.5, {className: '-=hidden'}).to(e, 1.5, {className: '+=hidden'});

    //   if (i == elems.length - 1) {
    //     tl2.to(e, 1.5, {onComplete: () => tl2.restart()});
    //   }
    // }
    // tl2.pause();


    // const tl1 = new TimelineMax()
    //   .fromTo('#banner', 1, {backgroundColor: "#d4fc79"}, {height: "70px", marginTop: "70px", backgroundColor: "#96e6a1", zIndex: 1000}, "startResize")
    //   .to("#banner .text", 1, {scale: 0.75, marginTop: "0", float: "left"}, "startResize")
    //   .to(
    //     "#banner .collapse-content",
    //     0.25,
    //     {top: "-70px", opacity: 0, display: "none", onComplete: () => tl2.play()}
    //   )

    // const controller = new ScrollMagic.Controller();

    // new ScrollMagic.Scene({
    //   triggerElement: '#banner',
    //   triggerHook: 0
	  // })
    // .setPin("#banner")
    // .setTween(tl1)
    // .addTo(controller);

    var banner = document.getElementById("sticky-banner"),
      news = document.getElementsByClassName("news"),
      tl2 = new TimelineMax({repeat: -1});

    tl2
      .to(banner, 0.01, {autoAlpha: 1})
      .staggerFromTo(news, 1,
        {autoAlpha: 0, y: -5},
        {autoAlpha: 1, y: 0},
        5) // News appears and stays for 5s
      .to(news, 0.5, {autoAlpha: 0}, "+=5") // Set delay to make last news stay for 5s
      .to(banner, 0.01, {autoAlpha: 0.5}
    )

  }

  render() {
    return (
      <div>
        <TopBanner/>
        <IntroSlider />
        <ProductIntro />
        <CourseTeasers titled={true} limit={5}/>
        <Banner />
        <CoachTeasers titled={true} limit={6} />
        <Map/>
      </div>
    );
  }
}

TopPage.route = <Route exact path="/" component={TopPage}/>;

export default TopPage;



