import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Carousel, Glyphicon} from 'react-bootstrap';

import events from './events.json';

const carouselItems = (event) => {
  const range = [...Array(event.slides).keys()];
  return range.map((picIdx) => (
    <Carousel.Item key={picIdx}>
      <img alt="" width={600} height={400} src={`/img/events/${event.id}/${picIdx + 1}.jpg`} />
    </Carousel.Item>
  ))
};

class EventPage extends Component {
  constructor(props) {
    super(props);

    const eventId = props.match.params.id;
    const event = events.find(c => c.id === eventId);

    if (event === undefined) {
      window.location = '/';
    }

    this.state = event;

    fetch(`/events/${eventId}.html`)
      .then(response => response.text())
      .then(intro => this.setState({intro}));
  }

  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div className="star-bg">
      <Grid>
        <Row>
          <Col>
            <h2 className="title-underline uppercase">Các sự kiện gần đây</h2>
          </Col>
          <Col md={12} className="text-center">
            <h2 className="highlight-text">{this.state.name}</h2>
          </Col>
         </Row>
        {this.state.slides > 0 &&
          <Row className="space-top">
            <Col md={6} mdOffset={3}>
              <Carousel interval={3500}>
                {carouselItems(this.state)}
              </Carousel>
            </Col>
            <Col md={2}>
              <h4 className="date"><Glyphicon glyph="calendar"/>{this.state.date}</h4>
              <p>{this.state.start}-{this.state.end}</p>
              <p><Glyphicon glyph="map-marker"/>{this.state.location}</p>
            </Col>
          </Row>
        }
        <Row className="margin-top margin-bottom">
            <Col md={8} mdOffset={2}>
            <div dangerouslySetInnerHTML={{__html: this.state.intro}} />
          </Col>
        </Row>
      </Grid>
      </div>
    );
  }
}

EventPage.route = <Route exact path="/events/:id" component={EventPage}/>;

export default EventPage;



