import React from 'react';
import {Glyphicon} from 'react-bootstrap';

const Banner = () => {
  return (
    <div id="sticky-banner">
      <div className="content-wrapper">
        <div className="news">
          <div className="news-content">
            <p className="collapse-content">Tự làm đồ thủ công, mỹ thuật sáng tạo, biểu diễn văn nghệ… làm quà tặng và gây quỹ quà tặng cho Trung tâm Bảo trợ xã hội địa bàn Hà Nội.</p>
            <a href="#/membership"><h5><Glyphicon glyph="arrow-right" />Lịch hoạt động chương trình hội viên</h5></a>
          </div>
        </div>
      </div>
  </div>
  );
};

export default Banner;



