import React from 'react';
import {Grid, Row, Button, Col, Carousel, Image} from 'react-bootstrap';

const Banner = () => {
  return (
    <div id="banner" className="text-center">
    <Grid>
      <Row className="banner-content">
        <Col sm={6} md={6} mdOffset={2}>
          <h2 className="margin-top text">Trải Nghiệm Hè 2017 cùng PDI</h2>
          <div className="reg-container margin-bottom text">
            <Button href="#/contact" bsClass="reg-button uppercase">Đăng kí</Button>
          </div>
        </Col>
        <Col sm={4} md={4} className="collapse-content">
          <Carousel interval={3000} controls={false} indicators={false}>
            <Carousel.Item>
              <Image alt="" width="100%" src={`/img/courses/3/3_1.jpg`} circle />
            </Carousel.Item>
            <Carousel.Item>
              <Image alt="" width="100%" src={`/img/courses/3/3_2.jpg`} circle />
            </Carousel.Item>
            <Carousel.Item>
              <Image alt="" width="100%" src={`/img/courses/3/3_5.jpg`} circle />
            </Carousel.Item>
          </Carousel>
        </Col>
      </Row>
    </Grid>
  </div>
  );
};

export default Banner;



