import React, {Component} from 'react';
import {Route} from 'react-router-dom';
//import {Grid, Row} from 'react-bootstrap';

import CoachTeasers from './CoachTeasers';

class CoachesPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
        <div>
          <h2 className="title-underline uppercase">Đội ngũ giảng viên</h2>
          <CoachTeasers limit={-1} />
        </div>
    );
  }
}

CoachesPage.route = <Route exact path="/coaches" component={CoachesPage}/>;

export default CoachesPage;



