import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col} from 'react-bootstrap';

class ContactPage extends Component {
  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div className="contact-page">
      <Grid>
        <Row>
          <Col md={12}>
            <h2 className="uppercase title-underline">Đăng kí</h2>
            <iframe className="iframe" src="https://docs.google.com/forms/d/e/1FAIpQLSfUX3RjcSlZTtatnsk_SUJdVXsR24RxTaKJqUgnfFYe1gX57g/viewform?embedded=true" width="100%" height="600" frameBorder="0" marginHeight="0" marginWidth="0" >Loading...</iframe>
          </Col>
        </Row>
      </Grid>
    </div>
    );
  }
}

ContactPage.route = <Route exact path="/contact" component={ContactPage}/>;

export default ContactPage;



