import React from 'react';
import {Glyphicon} from 'react-bootstrap';

const Banner = () => {
  return (
    <div id="sticky-banner">
      <div className="content-wrapper">
        <div className="news">
          <div className="news-content">
            <p><Glyphicon glyph="calendar"/>Thứ 7, 15/7/2017, 8.30AM – 10.30AM, <Glyphicon glyph="map-marker" />Tầng 3, 25T2 Nguyễn Thị Thập, Cầu Giấy, Hà Nội: Làm đồ thủ công, mỹ thuật sáng tạo.</p>
          </div>
        </div>
        <div className="news">
          <div className="news-content">
            <p><Glyphicon glyph="calendar"/>Chủ nhật, 30/7/2017, 8.30AM – 11.00AM, <Glyphicon glyph="map-marker" />Địa điểm công cộng: Hoạt động gây quỹ quà tặng.</p>
          </div>
        </div>
        <div className="news">
          <div className="news-content">
            <p><Glyphicon glyph="calendar"/>Chủ nhật, 13/8/2017, 8.30AM – 10.30AM, <Glyphicon glyph="map-marker" />Tầng 3, 25T2 Nguyễn Thị Thập, Cầu Giấy, Hà Nội: Tập duyệt biểu diễn văn nghệ.</p>
          </div>
        </div>
        <div className="news">
          <div className="news-content">
            <p><Glyphicon glyph="calendar"/>Chủ nhật, 27/8/2017, 8.30AM – 11.00AM, <Glyphicon glyph="map-marker" />Trung tâm bảo trợ xã hội: Giao lưu, tặng quà các bạn nhỏ tại Trung tâm BTXH.</p>
          </div>
        </div>
      </div>
  </div>
  );
};

export default Banner;



