import React from 'react';
import {Row, Col, Grid, Glyphicon} from 'react-bootstrap';

const Footer = () => {
  return (
    <div>
      <div id="footer">
        <Grid>
          <Row>
            <Col md={4}>
              <h3 className="uppercase">Giới thiệu về PDI</h3>
              <p>
                <span>Học viện phát triển cá nhân PDI</span>
                <a href="//www.facebook.com/PDI.edu.vn/"><img src="./img/fb.svg" width="35" height="35" alt="facebook" /></a>
              </p>
            </Col>

            <Col md={4}>
              <h3 className="uppercase">Liên kết</h3>
              <ul className="uppercase list-unstyled">
                <li><a href="/#/">Trang chủ</a></li>
                <li><a href="/#/about">Giới thiệu</a></li>
                <li><a href="/#/courses">Các khoá học</a></li>
                <li><a href="/#/events">Sự kiện</a></li>
                <li><a href="/#/blog">Chia sẻ</a></li>
              </ul>
            </Col>

            <Col md={4}>
              <h3 className="uppercase">Địa chỉ liên hệ</h3>
              <p className="text-capitalize"><Glyphicon glyph="map-marker" />Phòng A15, Tầng 2, Toà nhà 25T2, Cụm N05, Đường Hoàng Đạo Thuý, Quận Cầu Giấy, Hà Nội</p>
              <p><a href="mailto:info@pdi.edu.vn?Subject=Hello%20PDI" target="_top"><Glyphicon glyph="envelope" /> info@pdi.edu.vn</a><br/>
              <Glyphicon glyph="earphone" /> +84 4 3833 0555</p>
              <h5><Glyphicon glyph="flash" />Hotline 088 858 0055</h5>
            </Col>
          </Row>
        </Grid>
      </div>
      <div>
        <Grid>
          <Row className="copyright space-top">
            <Col>
              <p className="uppercase">© 2017 All rights reserved by PDI</p>
            </Col>
            </Row>
        </Grid>
      </div>
    </div>
  );
};

export default Footer;



