import React from 'react';
import {Link} from 'react-router-dom';
import {Grid, Row, Col, Image} from 'react-bootstrap';

import coaches from './coaches.json';

const CoachTeaser = ({coach}) => (
  <Col sm={4} md={4} className="coach-teaser text-center">
    <Image alt="Coach" src={`./img/coaches/coach${coach.id}.jpg`} circle/>
      <h4 className="coach-name">{coach.name}</h4>
      <blockquote>
        <p>{coach.teaser}</p>
        <footer className="author">{coach.author}</footer>
      </blockquote>
  </Col>
);

const CoachTeasers = ({titled, limit}) => {
  return (
    <div className="panel-even">
      <Grid>
        <Row className="clearfix">
          {titled && <h2 className="uppercase title-underline">Đội ngũ giảng viên</h2>}
        </Row>

        <Row className="clearfix space-top space-bottom">
          {coaches.slice(0, 3).map(c =>
            <CoachTeaser key={c.id} coach={c} />
          )}
        </Row>

        <Row className="clearfix space-top space-bottom">
          {coaches.slice(3, 6).map(c =>
            <CoachTeaser key={c.id} coach={c} />
          )}
        </Row>

        <Row className="clearfix space-top">
          {limit > 0 && limit < coaches.length &&
            <p className="uppercase more"><Link to="/about@about-team">Xem thêm...</Link></p>
          }
        </Row>
      </Grid>
    </div>
  );
};

export default CoachTeasers;



