const anchor = (...ids) => {
  const id = ids.find(id => window.location.href.indexOf(`@${id}`) >= 0);
  if (id) {
    document.getElementById(id).scrollIntoView();
  } else {
    window.scrollTo(0, 0);
  }
};

const withoutAnchor = (href) => {
  const anchorIdx = href.indexOf('@');
  if (anchorIdx > 0) {
    return href.substring(0, anchorIdx);
  } else {
    return href;
  }
};

export default anchor;
export {withoutAnchor};



